.Input{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}
.Label{
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
}

.InputElement{
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 10px;
    display: block;
    box-sizing: border-box;
    width: 100%;
}

.InputElement:focus{
    outline: none;
    background-color:#ccc;
}
.Invalid{
    border: 1px solid red;
    background-color: salmon;
}